import React, {
	useEffect,
	useMemo
	//  useState
} from "react";
import { Helmet } from "react-helmet";
import QuickView from "../../../components/features/product/QuickViewNew";
// import Service from "../../../components/features/service";
import Slider from "../components/banners/Slider";
import MiniSlider from "../components/banners/MiniSlider";
import { useSelector } from "react-redux";
// import BannerTwo from "../components/banners/BannerTwo";
// import BrandFour from "../components/brands/BrandFour";
// import LeftSideCollections from "../components/LeftSideCollections";
import Banners from "../components/banners/Banners";
// import BrandThree from "../components/brands/BrandThree";
// import BannerSlider from "../components/banners/bannerSlider";
import BlockBannerCollection from "../components/blocks/BlockBannerCollection";
import BlockNewsWidthCollection from "../components/blocks/BlockNewsWidthCollection";
// import BrandThree from "../components/brands/BrandThree";
import BrandTwo from "../components/brands/BrandTwo";

function HomePageDefault() {
	const marketData = useSelector(state => state.marketData);
	// const banners = useSelector(state => state.any.banners);
	const { blocks } = useSelector(state => state.blocks);
	const bigBlock = blocks?.find(block => block.title === "big_block");
	const bigBlockTwo = blocks?.find(block => block.title === "big_block_two");
	const bigBlockThree = blocks?.find(
		block => block.title === "big_block_three"
	);
	const bigBlockFour = blocks?.find(
		block => block.title === "big_block_four"
	);
	const miniBlock = blocks?.find(block => block.title === "mini_block");
	// const brands = banners.filter(banner => banner.position === "center");
	const head = useMemo(() => {
		return {
			title: marketData.name,
			description: marketData.settings && marketData.settings.description,
			keyword: marketData.settings && marketData.settings.keyword,
			favicon: marketData.favicon
		};
	}, []);

	useEffect(() => {
		document.querySelector("body").classList.add("menu-open");
		return () => {
			document.querySelector("body").classList.remove("menu-open");
		};
	}, []);

	return (
		<>
			<Helmet>
				<title>{head.title}</title>
				<meta name="description" content={head.description} />
				<meta name="keywords" content={head.keyword} />
				<meta name="apple-mobile-web-app-title" content={head.title} />
				<meta name="application-name" content={head.title} />
			</Helmet>
			<div className="home-page-one">
				<div className="container slider-container">
					<div className="row">
						<div className="offset-lg-2 col-lg-10 col-12">
							<Slider />
						</div>
					</div>
				</div>
				<div className="container mini-sliders-container">
					<div className="row">
						<div className="offset-lg-2 col-lg-10 col-12">
							<MiniSlider
								block={miniBlock}
								options={{
									pagination: false,
									perPage: 6,
									focus: "left",
									arrows: false,
									gap: "10px",
									breakpoints: {
										991: {
											perPage: 5
										},
										768: {
											perPage: 4
										},
										560: {
											perPage: 3.5
										}
									}
								}}
							/>
						</div>
					</div>
				</div>
				<div className="container banners-top">
					<Banners position="top" />
				</div>

				<div className="container brands">
					<BrandTwo position={`center`} setWidth={1} viewCount={8} />
					{/* <BrandThree block={brands} /> */}
					{/* <BannerSlider
						banner={brands}
						position={"center"}
						options={{
							pagination: false,
							perPage: 10,
							focus: "left",
							arrows: false,
							gap: "10px",
							breakpoints: {
								991: {
									perPage: 5
								},

								560: {
									perPage: 5.5
								}
							}
						}}
					/> */}
				</div>

				<div className="container collection-tabs">
					<BlockNewsWidthCollection
						collection={bigBlock}
						title={bigBlock?.name}
					/>
				</div>

				<div className="container block-collection-container">
					<BlockBannerCollection
						block={bigBlockTwo}
						menuCol={"col-lg-3 col-6"}
						bannerCol={"col-lg-3 col-6"}
						collectionCol={"col-lg-6 col-12"}
						collectionSize={"col-lg-4 col-md-3 col-6"}
					/>
				</div>
				<div className="container block-collection-container">
					<BlockBannerCollection
						block={bigBlockThree}
						menuCol={"col-lg-3 col-6"}
						bannerCol={"col-lg-3 col-6"}
						collectionCol={"col-lg-6 col-12"}
						collectionSize={"col-lg-4 col-md-3 col-6"}
					/>
				</div>
				<div className="container banners-top">
					<Banners position="bottom" />
				</div>
				<div className="container block-collection-container">
					<BlockBannerCollection
						block={bigBlockFour}
						menuCol={"col-lg-3 col-6"}
						bannerCol={"col-lg-3 col-6"}
						collectionCol={"col-lg-6 col-12"}
						collectionSize={"col-lg-4 col-md-3 col-6"}
					/>
				</div>
			</div>
			<QuickView />
		</>
	);
}

export default HomePageDefault;
