import React from "react";
import ShimmerProduct from "../../../../components/features/loaders/shimmer";
import { useSelector } from "react-redux";
import Banner from "../banners/Banner";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { brandSlider } from "../../../../components/options";

function BrandTwo({ position, setWidth, viewCount }) {
	let banners = useSelector(state => state.any.banners)
		.filter(banner => banner.position === position)
		.sort((a, b) => a.sort - b.sort);

	if (!banners?.length) {
		[1, 2, 3, 4, 5].map((_, index) => <ShimmerProduct key={index} />);
	}

	if (setWidth) {
		const toMatrix = (arr, width = setWidth) =>
			arr.reduce(
				(rows, key, index) =>
					(index % width === 0
						? rows.push([key])
						: rows[rows.length - 1].push(key)) && rows,
				[]
			);

		banners = toMatrix(banners);
	}

	return (
		banners?.length > 0 && (
			<Splide
				className="brand-block w-100"
				options={{
					...brandSlider,
					gap: "10px",
					breakpoints: {
						560: {
							perPage: 4.5
						},
						768: {
							perPage: 5.5
						},
						991: {
							perPage: 6.3
						},

						1100: {
							perPage: 6.5
						},
						1920: {
							perPage: viewCount ? viewCount : 7
						}
					}
				}}
			>
				{banners.map((item, index) => (
					<SplideSlide key={index}>
						{setWidth ? (
							item.map((item, index) => (
								<Banner item={item} key={index} size="300x_" />
							))
						) : (
							<Banner item={item} key={index} size="800x_" />
						)}
					</SplideSlide>
				))}
			</Splide>
		)
	);
}

export default BrandTwo;
